
html, body, .App {
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
--scroll-behavior: smooth;

}

body {
  font-family: helvetica, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  line-height: 35px;
}

.arrow {
  position: absolute;
  bottom: 50px;
  left: calc(50% - 17.5px);
  width: 35px;

  animation: arrow 1s infinite;
  animation-timing-function: ease-in-out;
  -webkit-animation: arrow 1s infinite;

  opacity: 0;
  transition: opacity 2s ease-in-out;
 -moz-transition: opacity 2s ease-in-out;
 -webkit-transition: opacity 2s ease-in-out;
}

.arrow img {
  width: 100%;
}

@keyframes arrow{
  50% { bottom: 40px; }
}
@-webkit-keyframes arrow{
  0% { bottom: 50px; }
  50% { bottom: 40px; }
  100% { bottom: 50px; }
}


/* TOP OF THE DOC */
#top {
  text-align: center;
  width: 30%;
  margin: auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* TYPED STYLES */
.typed-cursor{
  opacity: 1;
  animation: typedjsBlink 0.5s infinite;
  -webkit-animation: typedjsBlink 0.5s infinite;
  animation: typedjsBlink 0.5s infinite;
}
@keyframes typedjsBlink{
  50% { opacity: 0.0; }
}
@-webkit-keyframes typedjsBlink{
  0% { opacity: 1; }
  50% { opacity: 0.0; }
  100% { opacity: 1; }
}

.typed {
  font-family: Menlo, Consolas, DejaVu Sans Mono, monospace;
  font-size: 20px;
  line-height: 35px;
}

.typed b {
  color: rgb(220, 175, 100);
}

.typed i {
  color: rgb(200, 100, 100);
}

.typed mark {
  background: none;
  color: rgb(102, 200, 115);
}

.description {
 opacity: 0;
 transition: opacity 2s ease-in-out;
-moz-transition: opacity 2s ease-in-out;
-webkit-transition: opacity 2s ease-in-out;
}

/* MIDDLE*/
#body {
  width: 100%;
  background: #1c1b20;
  color: #4c4a57;
  border: 1px solid white;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.single-project {
  border: 1px solid white;
  width: 25%;
  text-align: center;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;
}

.single-project h3{
  font-family: Menlo, Consolas, DejaVu Sans Mono, monospace;
  margin: 0px;
  padding: 0px;
  color: white;
}

.pic img{
  width: 100%;
}

.single-project summary {
  padding: 10px 20px;
}

.single-project summary a {
  color: white;
  text-decoration: none;
}

.links {
  display: block;
  margin: 30px 0px;
}

.links img {
  width:35px;
  display: inline-block;
  margin: 0px 10px;
  opacity: 1;
}

.tools {
  display: block;
  width: 100%;
  padding: 50px;
  text-align: center;
  font-size: 15px;
  border-top: 1px solid white;
}

.tools h3{
  display: block;
  font-weight: normal;
}

.tools ul {
  font-family: Menlo, Consolas, DejaVu Sans Mono, monospace;
  font-weight: bold;
  width: 75%;
  margin: 0px auto;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.tools li {
  width: 20%;
  list-style-type: none;
  display: inline-block;
  color: white;

  padding: 20px;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;
}

.tools li img {
  width: 50%;
  margin: 20px auto;
  display: block;
}

.tools li:hover {
  animation-name: tada;
  -webkit-animation-name: tada;
  animation-duration: 1s;
  animation-iteration-count: 1;
}


@-webkit-keyframes tada {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  10%,
  20% {
    -webkit-transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
  }

  30%,
  50%,
  70%,
  90% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
  }

  40%,
  60%,
  80% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

@keyframes tada {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  10%,
  20% {
    -webkit-transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
  }

  30%,
  50%,
  70%,
  90% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
  }

  40%,
  60%,
  80% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

/* BOTTOM OF THE PAGE */
#footer {
  color: white;
  background: linear-gradient(to bottom right, #f024ce, transparent 60%, #9eff00),
               linear-gradient(to top right, #ef4146, transparent 25%, #ffc700) #ff3d00;
}

.widgets {
  display: block;
  text-align: center;
  padding: 40px 0px;
}

.widgets div {
  display: inline-block;
  margin: 20px;
  text-align: left;
}

.widgets h5 {
  margin: 0px;
  padding: 0px;
}

.book img{
  height: 455px;
}

.social {
  display: block;
}

.social ul {
  width: 100%;
  margin: 0px;
  padding: 30px 200px;
  border-top: 1px solid white;

  display: flex;
  justify-content:space-between;
  align-content: space-between;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;
}

.social li {
  list-style-type: none;
  display: inline-block;
  vertical-align: middle;
}

.social li img {
  width: 30px;
  display: inline-block;
}

.social li a {
  color: white;
  text-decoration: none;
}

.social li a:hover {
  opacity: .5;
}
