@media screen and (max-width: 1000px) {
  .tools li {
    width: 25%;
  }
}

@media screen and (max-width: 1000px) {
  .single-project {
    width: 33.33%;
  }
  .tools {
    padding: 30px;
  }
  .social ul {
    padding: 30px 70px;
  }
}

@media screen and (max-width: 850px) {
  .tools li {
    padding: 10px;
  }
}

@media screen and (max-width: 800px) {
  #top {
    width: 100%;
  }
  .single-project {
    width: 50%;
  }
  .tools li {
    padding: 0px;
  }
  .social ul {
    padding: 30px 50px;
  }
}

@media screen and (max-width: 700px) {
  .tools ul {
    width: 100%;
  }
}

@media screen and (max-width: 525px) {
  body {
    font-size: 15px;
    line-height: 25px;
  }
  #top {
    padding: 30px;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;
  }
  .typed {
    font-size: 15px;
    line-height: 25px;
  }
  .single-project {
    width: 100%;
  }
  .tools {
    padding: 20px;
  }
  .tools h3{
    margin-top: 0px;
  }

}

@media screen and (max-width: 450px) {
  #top {
    display: block;
    padding-top: 150px;
  }
  .typed {
    font-size: 12px;
    line-height: 20px;
  }
  .links {
    margin-top: 20px;
    margin-bottom: 0px;
  }
  .single-project summary {
    padding-bottom: 20px;
  }
  .tools ul {
    display: block;
  }
  .tools li {
    width: 50%;
    padding: 20px;
  }
  .social ul {
    padding: 20px;
  }
}
